
/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –º–∏–Ω–∏–∞—Ç—é—Ä (–≥—Ä—É–ø–ø–∞) */
.yarl__thumbnails {
    max-width: 100%;              /* –ø–æ–¥—Å—Ç—Ä–æ–π –ø–æ–¥ —Ä–µ–∞–ª—å–Ω—É—é —à–∏—Ä–∏–Ω—É —Ñ–æ—Ç–æ */
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: flex-start !important;
    padding-left: 0 !important;
}
.yarl__thumbnails_container {
    justify-content: flex-start !important;
    padding-left: 0 !important;
    scroll-snap-type: none !important;
}

.yarl__thumbnails {
    scroll-behavior: auto !important; /* —É–±–∏—Ä–∞–µ–º –ø–ª–∞–≤–Ω—É—é –ø—Ä–æ–∫—Ä—É—Ç–∫—É */
}

.yarl__thumbnails_track {
    justify-content: start !important; /* —É–±–∏—Ä–∞–µ–º –∞–≤—Ç–æ—Ü–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ */
}


/* –£–±–∏—Ä–∞–µ–º –∞–Ω–∏–º–∞—Ü–∏—é –ø—Ä–æ–∫—Ä—É—Ç–∫–∏ –∏ auto-centering */
.yarl__thumbnails {
    scroll-behavior: auto !important;
}

.yarl__thumbnails_track {
    justify-content:end !important;
    transition: none !important;
    transform: none !important;
}




.yarl__button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0; /* —É–±–∏—Ä–∞–µ–º –ª–∏—à–Ω–∏–µ –æ—Ç—Å—Ç—É–ø—ã */
    width: 76px;
    height: 76px;
    margin: 0 20px; /* –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–µ –æ—Ç—Å—Ç—É–ø—ã –æ—Ç –∫—Ä–∞—ë–≤ */
    background-color: rgba(0, 0, 0, 0.35);
    border-radius: 50%;
    transition: background-color 0.3s ease;
}

/* –ü—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ ‚Äî –Ω–µ –¥–µ–ª–∞–µ–º —Ñ–æ–Ω —Å–ª–∏—à–∫–æ–º –ø–ª–æ—Ç–Ω—ã–º */
.yarl__button svg {
    width: 38px;
    height: 38px;
    stroke-width: 2.2;
    margin: 0; /* —É–±–∏—Ä–∞–µ–º –≤–æ–∑–º–æ–∂–Ω—ã–π —Å–¥–≤–∏–≥ */
}

@media (max-width: 768px) {
    .yarl__button {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        width: 36px;
        height: 36px;
        margin: 0 15px; /* –º–µ–Ω—å—à–µ –æ—Ç—Å—Ç—É–ø—ã –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö */
        background-color: rgba(0, 0, 0, 0.35);
        border-radius: 50%;
        transition: background-color 0.3s ease;
    }

    .yarl__button svg {
        width: 18px;
        height: 18px;
        stroke-width: 2.2;
        margin: 0;
    }
}
