/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –ø–æ–ª–æ—Å–æ–∫ */
.slick-dots {
    position: absolute;
    bottom: 0;
    width: 100%;
    display: flex !important;
    justify-content: flex-start; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ —Å–ª–µ–≤–∞ */
    align-items: center;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    height: 4px; /* –í—ã—Å–æ—Ç–∞ –ø–æ–ª–æ—Å–∫–∏ –¥–ª—è —Ñ–æ–Ω–∞ */
}

/* –í–Ω–µ—à–Ω—è—è —Å–µ—Ä–∞—è –ø–æ–ª—É–ø—Ä–æ–∑—Ä–∞—á–Ω–∞—è –ø–æ–ª–æ—Å–∫–∞ */
.slick-dots::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 4px; /* –¢–æ–ª—â–∏–Ω–∞ –ø–æ–ª–æ—Å–∫–∏ */
    background-color: rgba(0, 0, 0, 0.2); /* –ë–æ–ª–µ–µ —Ç–µ–º–Ω—ã–π –∏ –º–µ–Ω–µ–µ –ø—Ä–æ–∑—Ä–∞—á–Ω—ã–π —Å–µ—Ä—ã–π */
    /*border-radius: 3px;*/
}

/* –ö–∞–∂–¥–∞—è –ø–æ–ª–æ—Å–∫–∞ */
.slick-dots li {
    position: relative;
    flex: 1;
    height: 4px; /* –¢–æ–ª—â–∏–Ω–∞ –ø–æ–ª–æ—Å–∫–∏ */
    background-color: transparent; /* –£–±–∏—Ä–∞–µ–º —Ñ–æ–Ω, —á—Ç–æ–±—ã –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å —Å–∏–Ω–∏–π */
    /*border-radius: 3px;*/
    transition: background-color 0.3s ease, transform 0.3s ease;
    cursor: pointer;
    margin: 0;
    padding: 0;
}

/* –ê–∫—Ç–∏–≤–Ω–∞—è –ø–æ–ª–æ—Å–∫–∞ */
.slick-dots li.slick-active {
    background-color: #007bff; /* –ù–∞—Å—ã—â–µ–Ω–Ω—ã–π —Å–∏–Ω–∏–π —Ü–≤–µ—Ç –¥–ª—è –∞–∫—Ç–∏–≤–Ω–æ–π –ø–æ–ª–æ—Å–∫–∏ */
    /*transform: scaleY(1.5); !* –£–º–µ–Ω—å—à–µ–Ω–Ω—ã–π —ç—Ñ—Ñ–µ–∫—Ç –º–∞—Å—à—Ç–∞–±–∏—Ä–æ–≤–∞–Ω–∏—è *!*/
}

/* –ö–Ω–æ–ø–∫–∞ –≤–Ω—É—Ç—Ä–∏ –ø–æ–ª–æ—Å–∫–∏ */
.slick-dots li button {
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    padding: 0;
    margin: 0;
    border: none;
    background: transparent;
    position: absolute;
    top: 0;
    left: 0;
}
/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –ø–æ–ª–æ—Å–æ–∫ END*/
